html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1rem;
  font-weight: 900;
}

a,
a:visited,
a:active {
  font-weight: 700;
  cursor: pointer;
  font-size: 0.875rem;
  color: var(--bright-blue);
}

:root {
  --font-red-hat-display: "Red Hat Display", sans-serif;
}

body {
  font-family: var(--font-red-hat-display);
}

:root {
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale-blue: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
}

.background {
  background-image: url("../images/pattern-background-mobile.svg");
  height: 50%;
  width: 100%;
  background-size: contain;
  background-repeat: repeat-x;
  position: absolute;
}
@media (min-width: 870px) {
  .background {
    background-image: url("../images/pattern-background-desktop.svg");
  }
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--pale-blue);
}

.card {
  max-width: 20.625rem;
  width: 100%;
  border-radius: 16px;
  background-color: white;
  display: flex;
  flex-direction: column;
  filter: drop-shadow(0 0 8px rgba(0, 0, 0, 0.2));
}
@media (min-width: 1440px) {
  .card {
    max-width: 25rem;
  }
}
.card .card__hero {
  margin-bottom: 1.5rem;
  height: 10rem;
  background-size: contain;
  border-radius: 16px 16px 0 0;
  width: 100%;
  background-image: url("../images/illustration-hero.svg");
}
@media (min-width: 1440px) {
  .card .card__hero {
    height: 12.1875rem;
  }
}
.card__header {
  font-weight: 900;
  text-align: center;
  margin-bottom: 1rem;
}
.card__text {
  padding: 0 1.875rem;
  text-align: center;
  color: var(--desaturated-blue);
  margin-bottom: 1.5rem;
  line-height: 1.6;
}
@media (min-width: 1440px) {
  .card__text {
    padding: 0 3.75rem;
  }
}
.card__first-container {
  display: flex;
  align-items: center;
}
.card__text-plan {
  margin-left: 1rem;
}
.card__annual {
  margin-bottom: 0;
}
.card__price {
  margin: 0;
  color: var(--desaturated-blue);
}
.card__plan-container {
  background-color: var(--very-pale-blue);
  border-radius: 16px;
  padding: 1rem;
  margin: 0 1.25rem 1.5rem 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 1440px) {
  .card__plan-container {
    margin: 0 3rem 1.25rem 3rem;
  }
}
.card__payment {
  margin: 0 1rem 1.25rem 1rem;
  box-shadow: 0 20px 10px -18px hsl(245, 83%, 62%);
  border-radius: 10px;
  height: 2.8125rem;
  background-color: var(--bright-blue);
  color: white;
  font-family: var(--font-red-hat-display);
  font-weight: 700;
  font-size: 0.875rem;
  border: none;
}
.card__payment:hover {
  cursor: pointer;
  background-color: hsla(245, 75%, 52%, 0.521);
}
@media (min-width: 1440px) {
  .card__payment {
    margin: 0 3rem 1.25rem 3rem;
  }
}
.card__cancel {
  text-align: center;
  margin-top: 0;
  margin-bottom: 2rem;
  color: var(--desaturated-blue);
  font-weight: 900;
}
.card__cancel:hover {
  color: var(--dark-blue);
}
.card__cancel-container {
  margin-top: 0;
  text-align: center;
}
.card__change:hover {
  text-decoration: none;
  color: hsla(245, 75%, 52%, 0.521);
}